<template>
  <div class="Page">
    <Category :prop-category="category" @emit-category="changeCategory" />
    <List :prop-datas="currentList" :prop-favorites="favorites" />
  </div>
</template>

<script>
import Vue from "vue";
import VueLazyload from "vue-lazyload";

import Category from "@/components/Category.vue";
import List from "@/components/List.vue";

Vue.use(VueLazyload);

export default {
  name: "Page",
  props: ["propDatas", "propCategory", "propFavorites"],
  components: {
    Category,
    List,
  },
  data() {
    return {
      datas: [],
      category: [],
      favorites: [],
      currentList: [],
      emitItem: {},
    };
  },
  methods: {
    changeCategory(target) {
      this.currentList = this.datas.filter(function (item) {
        return item.CAT2 == target;
      });
    },
    getCurrentList() {
      this.currentList = this.datas;
      this.currentList = this.currentList.slice(0, 9);
    },
  },
  created() {
    let vm = this;

    vm.datas = vm.propDatas;
    vm.category = vm.propCategory;
    vm.favorites = vm.propFavorites;

    // 直接使用 Array.slice() 可以取得資料，但沒辦法渲染在畫面上，待解
    // vm.currentList = vm.datas.slice(0, 9);
  },
  mounted() {
    this.getCurrentList();
  },
  beforeDestroy() {
    this.$bus.$emit("page:datas", this.datas);
  },
};
</script>

<style lang="scss">
.Page {
  max-width: 1140px;

  margin: {
    left: auto;
    right: auto;
  }

  @media (max-width: 991px) {
    max-width: 575px;
  }
}
</style>
